.service {
    padding-bottom: 20rem;

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 10rem;

        h2 {
            margin-right: auto;
        }
    }

    &__tabs {
        width: 60%;
        max-width: 58rem;
    }

    &__list {
        display: none;
        flex-wrap: wrap;

        &.active {
            display: flex;

            animation: fade-up 0.5s forwards;
        }

        @keyframes fade-up {
            0% {
                transform: translateY(40px);
                opacity: 0;
            }

            100% {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }

    &__item {
        width: 25%;
        
        border: 1px solid #ECECEC;
        background: $col_white;

        a {
            display: block;
            width: 100%;
            height: 100%;
            padding: 3rem 2.5rem;
        }

        svg {
            margin-bottom: 1.6rem;
        }

        h3 {
            margin-bottom: 1rem;
        }

        p {
            font-size: 1.4rem;
            line-height: 2rem;
            margin-bottom: 1.2rem;
        }

        &-footer {
            display: none;
            position: absolute;
            bottom: -3rem;
            left: 0;

            padding-left: 2.5rem;
            width: 100%;
            padding-bottom: 3rem;
            background-color: white;
        }

        transition: transform $tr_rules, box-shadow $tr_rules;

        &:hover,
        &:focus {
            border: 1px solid transparent;
            box-shadow: -10px 30px 50px rgba(62, 68, 141, 0.09);
            z-index: 10;
            transform: translateY(-3rem);

            .service__item-footer {
                display: block;

                span {
                    opacity: 0;
                    animation: fadeIn 1s 0.3s forwards;
                }
            }
        }
    }

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }

        40% {
            opacity: 0;
        }

        100% {
            opacity: 100%;
        }
    }
}
